import PropTypes from "prop-types";
import {
  Tooltip,
  Stack,
  InputAdornment,
  TextField,
  MenuItem,
  Box,
  Chip,
  ListItemText,
  Checkbox,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
// components
import Iconify from "../../../../components/Iconify";
import CurrencyPopover from "src/layouts/dashboard/header/CurrencyPopover";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

const INPUT_WIDTH = 160;

TicketTableToolbar.propTypes = {
  filterName: PropTypes.string,
  filterProvider: PropTypes.string,
  filterUser: PropTypes.string,
  filterEndDate: PropTypes.instanceOf(Date),
  filterStartDate: PropTypes.instanceOf(Date),
  onFilterName: PropTypes.func,
  onFilterProvider: PropTypes.func,
  onFilterEndDate: PropTypes.func,
  onFilterUser: PropTypes.func,
  onFilterStartDate: PropTypes.func,
  users: PropTypes.array,
  providers: PropTypes.any,
  placeholder: PropTypes.string,
  loadText: PropTypes.string,
  title: PropTypes.string,
  handleRefresh: PropTypes.func,
  isLoading: PropTypes.bool,
};

export default function TicketTableToolbar({
  users,
  providers,
  filterStartDate,
  filterEndDate,
  filterName,
  filterProvider,
  onFilterProvider,
  filterUser,
  onFilterName,
  onFilterUser,
  onFilterStartDate,
  onFilterEndDate,
  placeholder = "Search ...",
  loadText = "Load",
  title = "Filter list",
  handleRefresh,
  isLoading,
}) {
  const { t } = useTranslation();
  return (
    <Stack spacing={2} sx={{ py: 2.5, px: 3 }}>
      <Stack spacing={2} direction={{ xs: "column", md: "row" }}>
        <CurrencyPopover />
        <TextField
          fullWidth
          select
          label={t("field.operator")}
          value={filterProvider}
          onChange={onFilterProvider}
          SelectProps={{
            MenuProps: {
              sx: { "& .MuiPaper-root": { maxHeight: 260 } },
            },
          }}
          sx={{
            textTransform: "capitalize",
            maxWidth: { md: INPUT_WIDTH },
          }}
        >
          <MenuItem
            key="ALL"
            value=""
            sx={{
              mx: 1,
              my: 0.5,
              borderRadius: 0.75,
              typography: "body2",
              textTransform: "uppercase",
              fontWeight: "bold",
            }}
          >
            {t("params.all")}
          </MenuItem>
          {providers?.map((provide) => (
            <MenuItem
              key={provide}
              value={provide}
              sx={{
                mx: 1,
                my: 0.5,
                borderRadius: 0.75,
                typography: "body2",
                textTransform: "capitalize",
              }}
            >
              {provide}
            </MenuItem>
          ))}
        </TextField>

        <TextField
          fullWidth
          select
          label={t("field.user")}
          value={filterUser}
          onChange={onFilterUser}
          SelectProps={{
            MenuProps: {
              sx: { "& .MuiPaper-root": { maxHeight: 260 } },
            },
          }}
          sx={{
            textTransform: "capitalize",
            maxWidth: { md: INPUT_WIDTH },
          }}
        >
          {users?.map((usr) => (
            <MenuItem
              key={usr.uid}
              value={usr.uid}
              sx={{
                mx: 1,
                my: 0.5,
                borderRadius: 0.75,
                typography: "body2",
                textTransform: "capitalize",
              }}
            >
              {usr.full_name}
            </MenuItem>
          ))}
        </TextField>

        <DatePicker
          label={t("field.start_date")}
          value={filterStartDate}
          onChange={onFilterStartDate}
          renderInput={(params) => <TextField {...params} fullWidth />}
        />

        <DatePicker
          label={t("field.end_date")}
          value={filterEndDate}
          onChange={onFilterEndDate}
          renderInput={(params) => <TextField {...params} fullWidth />}
        />

        <Tooltip title={title}>
          <span>
            <LoadingButton
              startIcon={<Iconify icon={"ic:round-filter-list"} />}
              variant="contained"
              onClick={handleRefresh}
              loading={isLoading}
              size="large"
            >
              {loadText}
            </LoadingButton>
          </span>
        </Tooltip>
      </Stack>
      <TextField
        fullWidth
        value={filterName}
        onChange={(event) => onFilterName(event.target.value)}
        placeholder={placeholder}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <Iconify
                icon={"eva:search-fill"}
                sx={{ color: "text.disabled", width: 20, height: 20 }}
              />
            </InputAdornment>
          ),
        }}
      />
    </Stack>
  );
}
