import sumBy from "lodash/sumBy";
import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
// @mui
import { useTheme } from "@mui/material/styles";
import {
  Box,
  Tab,
  Tabs,
  Card,
  Table,
  Stack,
  Switch,
  Tooltip,
  Divider,
  TableBody,
  Container,
  IconButton,
  TableContainer,
  TablePagination,
  FormControlLabel,
} from "@mui/material";
// redux
import { useDispatch, useSelector } from "../../redux/store";
import { useTranslation } from "react-i18next";
import { useSnackbar } from "notistack";
import { getTickets } from "../../redux/slices/ticket";
import { getUsers, getProviders } from "../../redux/slices/generale";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
// hooks
import useTabs from "../../hooks/useTabs";
import useSettings from "../../hooks/useSettings";
import useTable, { getComparator, emptyRows } from "../../hooks/useTable";
// components
import Page from "../../components/Page";
import Label from "../../components/Label";
import Iconify from "../../components/Iconify";
import Scrollbar from "../../components/Scrollbar";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
import {
  TableEmptyRows,
  TableHeadCustom,
  TableSkeleton,
  TableNoData,
  TableSelectedActions,
} from "../../components/table";
// sections
import TicketAnalytic from "../../sections/@dashboard/ticket/TicketAnalytic";
import {
  TicketTableRow,
  TicketTableToolbar,
} from "../../sections/@dashboard/ticket/list";
import extractErrorMessage from "../../utils/error";
// utils
import { fDateOnly } from "../../utils/formatTime";
// ----------------------------------------------------------------------

// ----------------------------------------------------------------------

export default function TicketSale() {
  const theme = useTheme();

  const { themeStretch } = useSettings();

  const navigate = useNavigate();

  const { t } = useTranslation();

  const TABLE_HEAD = [
    { id: "ref_code", label: t("field.ref_code"), align: "left" },
    { id: "transaction_id", label: t("field.transaction_id"), align: "center" },
    { id: "full_name", label: t("field.full_name"), align: "center" },
    { id: "created_on", label: t("field.created_on"), align: "center" },
    { id: "operator", label: t("field.operator"), align: "center" },
    { id: "phone", label: t("field.phone"), align: "center" },
    { id: "montant", label: t("field.montant"), align: "center" },
    { id: "commission", label: t("field.commission"), align: "center" },
    { id: "commission_sp", label: t("field.commission_sp"), align: "center" },
    { id: "status", label: t("field.status"), align: "center" },
    { id: "", align: "right" },
  ];
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const {
    dense,
    page,
    order,
    orderBy,
    rowsPerPage,
    setPage,
    //
    selected,
    setSelected,
    onSelectRow,
    onSelectAllRows,
    //
    onSort,
    onChangeDense,
    onChangePage,
    onChangeRowsPerPage,
  } = useTable({ defaultOrderBy: "tirage_name" });

  const { tickets, isLoading } = useSelector((state) => state.ticket);
  const { users, providers } = useSelector((state) => state.generale);

  const [tableData, setTableData] = useState([]);

  const [filterName, setFilterName] = useState("");
  const [filterProvider, setFilterProvider] = useState("");

  const [filterUser, setFilterUser] = useState("All");

  const [filterStartDate, setFilterStartDate] = useState(new Date());

  const [filterEndDate, setFilterEndDate] = useState(new Date());

  const { currentTab: filterStatus, onChangeTab: onFilterStatus } =
    useTabs("all");

  const handleRefresh = () => {
    dispatch(
      getTickets(
        page + 1,
        rowsPerPage,
        fDateOnly(filterStartDate, "yyyy-M-d"),
        fDateOnly(filterEndDate, "yyyy-M-d"),
        filterUser,
        filterName,
        filterProvider
      )
    );
    dispatch(getUsers());
    dispatch(getProviders());
  };

  useEffect(() => {
    handleRefresh();
  }, [dispatch, page, rowsPerPage, filterName]);

  useEffect(() => {
    if (tickets?.count > 0) {
      setTableData(tickets?.data || []);
    } else {
      setTableData([]);
    }
  }, [tickets]);

  const handleFilterName = (filterName) => {
    setFilterName(filterName);
    setPage(0);
  };

  const handleFilterProvider = (event) => {
    setFilterProvider(event.target.value);
  };

  const handleFilterUser = (event) => {
    setFilterUser(event.target.value);
  };

  const handleViewRow = (id) => {
    navigate(PATH_DASHBOARD.ticket.view(id));
  };

  const handleViewDetailRow = (id) => {
    navigate(PATH_DASHBOARD.ticket.detail(id));
  };

  const dataFiltered = applySortFilter({
    tableData,
    comparator: getComparator(order, orderBy),
    filterStatus,
  });

  const denseHeight = dense ? 56 : 76;

  const isNotFound =
    (!dataFiltered.length && !!filterStatus) ||
    (!isLoading && !dataFiltered.length);

  const getLengthTotal = () =>
    tableData.filter((item) => item.transaction_status === "COMPLETE").length;

  const getTotalPriceTotal = () =>
    sumBy(
      tableData.filter((item) => item.transaction_status === "COMPLETE"),
      "amount"
    );
  const getTotalCommissionTotal = () =>
    sumBy(
      tableData.filter((item) => item.transaction_status === "COMPLETE"),
      "percent"
    );
  const getTotalCommissionSpTotal = () =>
    sumBy(
      tableData.filter((item) => item.transaction_status === "COMPLETE"),
      "sp_percent"
    );

  return (
    <Page title={`Ticket: ${t("p_ticket.ticket_list")}`}>
      <Container maxWidth={false}>
        <HeaderBreadcrumbs
          heading={t("p_ticket.ticket_list")}
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("p_ticket.ticket"),
              href: PATH_DASHBOARD.ticket.root,
            },
            { name: t("p_ticket.ticket_list") },
          ]}
        />

        <Card sx={{ mb: 5 }}>
          <Scrollbar>
            <Stack
              direction="row"
              divider={
                <Divider
                  orientation="vertical"
                  flexItem
                  sx={{ borderStyle: "dashed" }}
                />
              }
              sx={{ py: 2 }}
            >
              <TicketAnalytic
                title={t("app.total_ticket")}
                percent={100}
                price={getLengthTotal()}
                icon="ic:round-receipt"
                color={theme.palette.info.main}
              />
              <TicketAnalytic
                title={t("app.total_montant")}
                percent={100}
                price={getTotalPriceTotal()}
                icon="eva:checkmark-circle-2-fill"
                color={theme.palette.success.main}
              />
              <TicketAnalytic
                title={t("app.total_commission")}
                percent={100}
                price={getTotalCommissionTotal() + getTotalCommissionSpTotal()}
                icon="mdi:cash-check"
                color={theme.palette.warning.main}
              />
            </Stack>
          </Scrollbar>
        </Card>

        <Card>
          <TicketTableToolbar
            filterName={filterName}
            filterProvider={filterProvider}
            filterUser={filterUser}
            filterStartDate={filterStartDate}
            filterEndDate={filterEndDate}
            onFilterName={handleFilterName}
            onFilterProvider={handleFilterProvider}
            onFilterUser={handleFilterUser}
            onFilterStartDate={(newValue) => {
              setFilterStartDate(newValue);
            }}
            onFilterEndDate={(newValue) => {
              setFilterEndDate(newValue);
            }}
            users={users}
            providers={providers}
            handleRefresh={handleRefresh}
            isLoading={isLoading}
            placeholder={t("params.search", {
              obj: t("p_ticket.ticket"),
            })}
            loadText={t("params.load")}
            title={t("params.filter_list")}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800, position: "relative" }}>
              {selected.length > 0 && (
                <TableSelectedActions
                  dense={dense}
                  numSelected={selected.length}
                  rowCount={tableData.length}
                  selectedText={t("params.selected")}
                  onSelectAllRows={(checked) =>
                    onSelectAllRows(
                      checked,
                      tableData.map((row) => row.id)
                    )
                  }
                />
              )}

              <Table size={dense ? "small" : "medium"}>
                <TableHeadCustom
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={tableData.length}
                  numSelected={selected.length}
                  onSort={onSort}
                  onSelectAllRows={(checked) =>
                    onSelectAllRows(
                      checked,
                      tableData.map((row) => row.id)
                    )
                  }
                />

                <TableBody>
                  {(isLoading ? [...Array(rowsPerPage)] : dataFiltered).map(
                    (row, index) =>
                      row ? (
                        <TicketTableRow
                          key={row.id}
                          row={row}
                          selected={selected.includes(row.id)}
                          onSelectRow={() => onSelectRow(row.id)}
                          onViewRow={() => handleViewRow(row.ref_code)}
                          onViewDetail={() => handleViewDetailRow(row.ref_code)}
                        />
                      ) : (
                        !isNotFound && (
                          <TableSkeleton
                            key={index}
                            sx={{ height: denseHeight }}
                          />
                        )
                      )
                  )}

                  <TableEmptyRows
                    height={denseHeight}
                    emptyRows={emptyRows(
                      page,
                      tableData.length,
                      tableData.length + tickets.count
                    )}
                  />

                  <TableNoData
                    isNotFound={isNotFound}
                    title={t("params.no_data")}
                    colSpan={12}
                  />
                </TableBody>
              </Table>
            </TableContainer>
          </Scrollbar>

          <Box sx={{ position: "relative" }}>
            <TablePagination
              rowsPerPageOptions={[
                5,
                25,
                50,
                100,
                { label: t("params.all"), value: 99999 },
              ]}
              component="div"
              count={tickets.count}
              rowsPerPage={rowsPerPage}
              page={page}
              onPageChange={onChangePage}
              onRowsPerPageChange={onChangeRowsPerPage}
              labelRowsPerPage={t("params.row_per_page")}
            />

            <FormControlLabel
              control={<Switch checked={dense} onChange={onChangeDense} />}
              label={t("params.dense")}
              sx={{ px: 3, py: 1.5, top: 0, position: { md: "absolute" } }}
            />
          </Box>
        </Card>
      </Container>
    </Page>
  );
}

// ----------------------------------------------------------------------

function applySortFilter({ tableData, comparator, filterStatus }) {
  const stabilizedThis = tableData.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });

  tableData = stabilizedThis.map((el) => el[0]);

  if (filterStatus === "PAID") {
    tableData = tableData.filter(
      (item) => item.paid === filterStatus && item.delete == "ACTIVE"
    );
  } else if (filterStatus === "DELETE") {
    tableData = tableData.filter((item) => item.delete === filterStatus);
  } else if (filterStatus !== "all") {
    tableData = tableData.filter(
      (item) => item.status === filterStatus && item.delete == "ACTIVE"
    );
  }

  return tableData;
}
